import React from 'react';
import { Contacts } from '../Contacts/contacts';
import { ProfileDataItem, ProfileDataItemCheckbox } from './profileDataItem/profileDataItem';

export const ProfileData = ({profile, isOwner, goToEditMode, s}) => {
    const {fullName, aboutMe, lookingForAJob, lookingForAJobDescription} = profile;
    return (
        <div className={s.profileFooter}>
            <div className={s.profileDescr}><div>Рабочая информация</div>{isOwner ? <div className={s.editButton} onClick={goToEditMode}>Редактировать</div> : null}</div>
            <ProfileDataItem itemDescr='Полное имя:' item={fullName} plug='Имя' s={s} />
            <ProfileDataItem itemDescr='Обо мне:' item={aboutMe} plug='Инфы нет ;(' s={s} />
            <ProfileDataItemCheckbox itemDescr='Ищу работу:' item={lookingForAJob} search='Ищу' dontSearch='Не ищу' s={s} />
            <ProfileDataItem itemDescr='Мои навыки:' item={lookingForAJobDescription} plug='Нет описания' s={s} />
            <div className={s.profileDescr}>Контакты</div>
            {Object.keys(profile.contacts).map( key => { return <Contacts key={key} contactTitle={key} contactValue={profile.contacts[key]} s={s} />})}
        </div>
    )
}